import React, { useRef, useState } from "react";
import ReactCrop from "react-image-crop";
import "react-image-crop/dist/ReactCrop.css";
import Dialog from "../../feedback/dialog/dialog.component";
import { getCroppedImg } from "../../../utils/functions";
import Spinner from "../../feedback/spinner/spinner.component";
import { deleteImageFromCloud } from "../../../dashboard/http-requests";

const ImageUploader = ({ onChange }) => {
  const fileInputRef = useRef(null);
  const [imgErrorMessage, setImgErrorMessage] = useState(false);
  const [imgSrc, setImgSrc] = useState(null);
  const [crop, setCrop] = useState({
    unit: "px",
    x: 25,
    y: 25,
    width: 100,
    height: 100,
  });
  const [crpdImg, setCrpdImg] = useState(null);

  const [imgUrl, setImgUrl] = useState(null);

  const [isUploading, setIsUploading] = useState(false);

  const imageRef = useRef(null);

  const [showDialog, setShowDialog] = useState(false);

  const handleImageSelect = (e) => {
    const file = e.target.files?.[0];
    if (!file) return;
    if (file.size > 1024 * 1024) {
      e.target.value = "";
      setImgErrorMessage(true);
      setImgSrc(""); // Clear image source
      return;
    }
    if (imgErrorMessage) setImgErrorMessage(false);

    setShowDialog(true);

    const reader = new FileReader();
    reader.addEventListener("load", () => {
      const imageUrl = reader.result?.toString() || "";
      setImgSrc(imageUrl);
    });
    reader.readAsDataURL(file);
  };

  const onCropComplete = async (crop) => {
    if (imageRef.current && crop.width && crop.height) {
      const crpdImg = await getCroppedImg(imageRef.current, crop);
      setCrpdImg(crpdImg);
    }
  };

  const handleUpload = async (e) => {
    e.preventDefault();
    if (!crpdImg) return;

    const formData = new FormData();
    formData.append("file", crpdImg);

    try {
      setIsUploading(true);
      const response = await fetch("http://localhost:8080/api/upload/image", {
        method: "POST",
        credentials: "include",
        body: formData,
      });
      const data = await response.json();

      if (data.status === "ULD201IMG") {
        setImgUrl(data.payload.imgUrl);
        setIsUploading(false);
        setShowDialog(false);
        onChange({
          target: {
            name: "imgUrl",
            value: data.payload.imgUrl,
          },
        });
      }
    } catch (error) {
      console.error("Upload failed:", error);
    }
  };

  const resetComponent = () => {
    setImgErrorMessage(null);
    setImgSrc(null);
    setCrop({
      unit: "px",
      x: 25,
      y: 25,
      width: 100,
      height: 100,
    });
    setCrpdImg(null);
    setImgUrl(null);
    setIsUploading(false);
    setShowDialog(false);
    imageRef.current = null;
    fileInputRef.current.value = "";
  };

  const handleDelete = async (e) => {
    if (imgUrl) {
      e.preventDefault();
      await deleteImageFromCloud({ imgUrl })
        .then(() => {
          resetComponent();
        })
        .catch((err) => {
          console.log(err);
        });
    } else resetComponent();
  };

  return (
    <>
      <div className="flex flex-row gap-3 justify-between items-center">
        <label className="text-xl" htmlFor="Image">
          Image
        </label>
        <div className="flex gap-3 justify-between items-center">
          {!imgUrl ? (
            <>
              <input
                className="border border-black px-1 w-60 text-xl"
                type="file"
                accept="image/*"
                onChange={handleImageSelect}
                ref={fileInputRef}
                required
              />
              {imgErrorMessage && (
                <p className="text-red-800">File size exceeds 1 MB!</p>
              )}
            </>
          ) : (
            <>
              <div className="flex gap-5">
                <img
                  className="w-20 h-20 rounded-full"
                  src={imgUrl}
                  alt="image"
                />
                <button onClick={handleDelete}>
                  <img
                    className="w-6 h-6"
                    src="data:image/png;base64,"
                    alt=""
                  />
                </button>
              </div>
            </>
          )}
        </div>
      </div>

      {showDialog && (
        <Dialog onClose={handleDelete}>
          <div className="bg-white  rounded p-2 md:p-20">
            {imgSrc && (
              <div className="relative max-w-[400px] ">
                <ReactCrop
                  crop={crop}
                  onChange={(c) => setCrop(c)}
                  aspect={1}
                  keepSelection={true}
                  onComplete={onCropComplete}
                >
                  <img
                    ref={imageRef}
                    src={imgSrc}
                    alt="Image"
                    style={{ width: "100%", height: "auto" }}
                  />
                </ReactCrop>
              </div>
            )}

            {isUploading ? (
              <div className="mt-10">
                <Spinner />
              </div>
            ) : (
              <button className="mt-10 my-button" onClick={handleUpload}>
                Upload
              </button>
            )}
          </div>
        </Dialog>
      )}
    </>
  );
};

export default ImageUploader;
